.SessionItem {
  position: relative; }
  .SessionItem .TimeIndicator {
    position: relative;
    float: left;
    width: 100px;
    text-align: center;
    padding-bottom: 80px; }
    .SessionItem .TimeIndicator .Indicator {
      width: 12px;
      height: 12px;
      margin: 0 auto;
      border-radius: 50%;
      background: #a821d2; }
    .SessionItem .TimeIndicator .Time {
      margin-top: 8px;
      color: #a821d2;
      font-size: 12px;
      font-weight: 700; }
    .SessionItem .TimeIndicator .Line {
      position: absolute;
      top: 44px;
      left: 0;
      right: 0;
      bottom: 0;
      width: 1px;
      background: #a821d2;
      margin: 0 auto; }
  .SessionItem .Speaker {
    float: left;
    margin-left: 8px;
    font-size: 14px; }
    .SessionItem .Speaker .Title {
      font-weight: 700;
      color: #22032b; }
    .SessionItem .Speaker .Name {
      margin-top: 12px;
      color: #a821d2; }
  .SessionItem .LaunchTitle {
    float: left;
    margin-left: 8px;
    color: #22032b;
    font-size: 14px;
    font-weight: 700;
    margin-top: 8px; }
  .SessionItem:last-child .TimeIndicator {
    padding-bottom: 20px; }
    .SessionItem:last-child .TimeIndicator .Line {
      display: none; }



